import ListMixin from '@/mixin/List.js'
import CountDown from '@/componets/uni/uni-countdown.vue'
export default {
  name: 'activityList',
  mixins: [ListMixin],
  components: {
    CountDown
  },
  data () {
    return {
      time: 0
    }
  },
  methods: {
    getData () {
      this.$http(this.$urlPath.assistList, {
        identity: this.$ids.identity,
        store_id: this.$ids.storeId,
        page: this.page.num
      }, null, (data) => {
        this.time = data.time
        this.loadSuccess(data.data)
      }, (errorCode, error) => {
        this.loadFail()
      }, () => {
		  uni.stopPullDownRefresh()
	  })
    },
    itemClick (item) {
      this.$push(this.$mRouter.activityDetail({
		 s: item.scenic_id,
		 g: item.goods_id,
		 p: item.assist_id
      }))
    }
  },
  onLoad() {
  	uni.startPullDownRefresh()
  }
}
